<template>
  <div class="contact">
    <el-form
      :model="contactForm"
      status-icon
      :rules="rules"
      ref="contactForm"
      label-width="120px"
      label-position="top"
    >
      <el-card class="contact-card" :body-style="{padding:'15px 10px'}">
        <div slot="header" class="clearfix">
          <span>Customer Information</span>
        </div>
        <el-form-item label="Company" prop="organization">
          <el-input placeholder="Company" v-model="contactForm.organization" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="Contact Name" prop="contact_name">
          <el-input placeholder="Contact Name" v-model="contactForm.contact_name" autocomplete="off"></el-input>
        </el-form-item>
        <el-row :gutter="20">
          <el-col :span="12">
            <el-form-item  label="Address" prop="address">
              <el-input  placeholder="Address" v-model="contactForm.address" autocomplete="off"></el-input>
            </el-form-item>
          </el-col>

          <el-col :span="12">
            <el-form-item   label="City" prop="city">
              <el-input placeholder="City" v-model="contactForm.city" autocomplete="off"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row :gutter="20">
          <el-col :span="8">
            <el-form-item  label="State" prop="state" required>
              <el-input  placeholder="State" v-model="contactForm.state" autocomplete="off"></el-input>
            </el-form-item>
          </el-col>

          <el-col :span="8">
            <el-form-item   label="Zip" prop="zipcode" required>
              <el-input placeholder="Zip" v-model="contactForm.zipcode" autocomplete="off"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="8">
            <el-form-item   label="Country" prop="country" required>
              <el-input placeholder="Country" v-model="contactForm.country" autocomplete="off"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row :gutter="20">
          <el-col :span="12">
            <el-form-item label="Email Address" prop="email" required>
              <el-input
                placeholder="Email Address"
                v-model="contactForm.email"
                autocomplete="off"
              ></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="Phone Number" prop="phone" required>
              <el-input placeholder="Phone Number" v-model="contactForm.phone" autocomplete="off"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
      </el-card>

      <el-card ref="info" class="contact-card" :body-style="{padding:'10px'}">
        <div slot="header" class="clearfix">
          <span>Equipment Information</span>
        </div>
        <el-row :gutter="20">
          <el-col :span="12">
            <el-form-item label="Model#" prop="model" required>
              <el-input
                placeholder="Model#"
                v-model="contactForm.model"
                autocomplete="off"
              ></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="Serial#" prop="serial" required>
              <el-input placeholder="Serial#" v-model="contactForm.serial" autocomplete="off"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
      </el-card>
      <el-card ref="info" class="contact-card" :body-style="{padding:'10px'}">
        <div slot="header" class="clearfix">
          <span>Contact Office</span>
        </div>
        <el-form-item class="customer_service" label="Customer Service" prop="customer_service">
          <el-checkbox-group v-model="contactForm.customer_service">
            <el-checkbox label="Factory" name="customer_service"></el-checkbox>
            <el-checkbox label="Sales / Marketing" name="customer_service"></el-checkbox>
          </el-checkbox-group>
        </el-form-item>
        <el-row :gutter="20">
          <el-col :span="12">
            <el-form-item label="Contact Name" prop="contact_name">
              <el-input
                placeholder="Contact Name"
                v-model="contactForm.contact_name"
                autocomplete="off"
              ></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="Contact Time & Date" prop="datetime">
             <el-date-picker
                    format="yyyy-MM-dd hh:mm A"
                    value-format="yyyy-MM-dd hh:mm A"
                    v-model="contactForm.datetime"
                    type="datetime"
                    placeholder="Select date and time"
                    :picker-options="pickerOption"
                    style="width:100%"
                  ></el-date-picker>
            </el-form-item>
          </el-col>
        </el-row>
        <el-form-item label="Message" prop="message">
          <el-input :rows="8" v-model="contactForm.message" type="textarea" placeholder="Message"></el-input>
        </el-form-item>
      </el-card>
      <vue-recaptcha
        align="center"
        :sitekey="this.$reCaptcha"
        :loadRecaptchaScript="true"
        ref="recaptcha"
        @verify="onVerify"
        @expired="onExpired"
      ></vue-recaptcha>
      <el-row :gutter="20">
        <el-col :span="12">
          <el-button type="success" @click="onSubmit">Send Details</el-button>
        </el-col>
        <el-col :span="12">
          <el-button type="danger" @click="resetForm">Clear Details</el-button>
        </el-col>
      </el-row>
    </el-form>
  </div>
</template>

<script>
import VueRecaptcha from "vue-recaptcha";
import axios from 'axios'
import moment from 'moment'
export default {
  components: { VueRecaptcha },
  name: 'Contact',
  data () {
    return {
      sending: false,
      contactForm: {
        captcha: null,
        contact_name: "",
        organization: "",
        address: "",
        city: "",
        state: "",
        zipcode: "",
        country: "",
        email: "",
        phone: "",
        model: "",
        serial: "",
        customer_service: [],
        datetime: null,
        message: "",
      },
      rules: {
        organization: [
          {
            required: true,
            message: "Please enter company name",
            trigger: "blur"
          },
          {
            min: 5,
            message: "Length should be at least 5 characters",
            trigger: "blur"
          }
        ],
        contact_name: [
          {
            required: true,
            message: "Please enter contact name",
            trigger: "blur"
          },
          {
            min: 5,
            message: "Length should be at least 5 characters",
            trigger: "blur"
          }
        ],
        address: [
          {
            required: true,
            message: "Please enter the address",
            trigger: "blur"
          },
          {
            min: 10,
            message: "Length should be at least 10 characters",
            trigger: "blur"
          }
        ],
        city: [
          {
            required: true,
            message: "Please enter the city",
            trigger: "blur"
          },
          {
            min: 5,
            message: "Length should be at least 5 characters",
            trigger: "blur"
          }
        ],
        email: [
          {
            required: true,
            message: "Please add email address",
            trigger: "blur"
          },
          {
            type: "email",
            message: "Please input correct email address",
            trigger: ["blur", "change"]
          }
        ],
        datetime: [
          { type: 'string', required: true, message: 'Please pick a date and time', trigger: ["blur", "change"] }
        ],
        customer_service: [
          { type: 'array', required: true, message: 'Please select at least one', trigger: 'change' }
        ],
        message: [
          { required: true, message: 'Please write your message', trigger: 'blur' }
        ]
      },
      pickerOption: {
        disabledDate(date) {
          let d1 = moment.unix(date.getTime() / 1000).format("YYYY-MM-DD")
          let d2 = moment().format("YYYY-MM-DD")
          return d1 < d2;
        }
      }
    }
  },
  mounted(){
    axios.defaults.baseURL = 'https://api.busfirms.com/message'
  },
  methods: {
    onSubmit() {
      this.$refs.contactForm.validate((valid) => {
        if (valid) this.send()
      })
    },
    send() {
      this.sending = true
      axios.post('/message', this.contactForm).then(response => {
        let type = 'error'
        this.sending = false
        this.resetRecaptcha()
        if (response.data.success == true) type = 'success'
        this.$message({
          message: response.data.message,
          type: type
        })
        this.resetForm()
      }).catch(response => {
        if (response.status == 404) this.$message({ message: 'Error', type: 'success' })
      })
    },
    resetForm() {
      this.$refs.contactForm.resetFields();
    },
    onVerify(response) {
      this.contactForm.captcha = response
    },
    onExpired() {
      this.contactForm.captcha = null
    },
    resetRecaptcha() {
      this.contactForm.captcha = null
      this.$refs.recaptcha.reset() // Direct call reset method
    }
  }
}
</script>

